body {
  overscroll-behavior: none;
  overflow-y: hidden;
  overflow-x: hidden;
  margin: 0% !important;
  padding: 0% !important;
}
* {
  margin: 0%;
  padding: 0%;
}

#blocker {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(1, 0, 0, 0.85);
}

#instructions {
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  text-align: center;
  cursor: pointer;
}

#welcome_text {
  font-family: "Rubik Glitch", cursive;
  font-size: 64px;

  color: #222;
  text-shadow: 0 0 2px #fff, 0 0 2px #fff, 0 0 3px #fff, 0 0 4px #0fa;
}

#manual {
  font-family: "Space Grotesk", sans-serif;
  color: #ccc;
  font-size: 48px;
  text-shadow: 0 0 1px #fff, 0 0 1px #fff, 0 0 2px #fff, 0 0 3px #0fa;
}

#webgl {
  display: block;
  width: 100vw;
  height: 100vh;
  margin: 0%;
  padding: 0%;
}

.loading-bar {
  position: absolute;
  margin: auto;
  /* top: 50%; */
  width: 100%;
  height: 100vh;
  z-index: 1000;
  background: #60100b;
  transform: scaleX(0);
  transform-origin: top left;
  transition: transform 0.5;
  will-change: transform;
}

.loading-bar.ended {
  transform-origin: top right;
  transition: transform 1.5s ease-in-out;
}

